<template lang="html">

<div class="page has-footer">
  <cube-scroll :data="List" :options="{scrollbar:{fade: true}}">
    <div class="top-slides">
      <cube-slide ref="slide" :data="Slides" @change="changeSlide" class="top-slides">
        <cube-slide-item v-for="(item, index) in Slides" :key="index" @click.native="clickHandler(item, index)">
          <a :href="item.LinkUrl" :style="{backgroundImage: 'url('+ item.CoverImage +')'}"> <img :src="item.CoverImage"> </a>
        </cube-slide-item>
      </cube-slide>
    </div>
    <tit has-bg more-link="/more" has-line>调度公开</tit>
    <list :is-empty="!List.length">
      <item :img-src="item.CoverImage"
      v-for="(item,index) in List" :key="index"
      :title="item.Title"
      :desc="item.Summary"
      :href="'/home/detail/' + index"
      v-if="index<3">
        <div class="text-footer" slot="text-footer">所属单位：三分厂</div>
        <div class="label" slot="label"> 已完成 </div>
      </item>
    </list>
    <tit has-bg more-link="/more" right-text="其它5个" has-line class="mt-md">我的待办</tit>
    <list :is-empty="!List.length">
      <item :img-src="item.CoverImage"
        v-for="(item,index) in List" :key="index"
        :title="item.Title"
        :desc="item.Summary"
        :href="'/home/detail/' + index"
        v-if="index>3">
        <div class="text-footer" slot="text-footer">所属单位：三分厂</div>
        <div class="label" slot="label"> 待处理 </div>
      </item>
    </list>

  </cube-scroll>

</div>
</template>

<script>
export default {

  data() {
    return {
      List: [],
      Slides:[]
    }
  },
  created () {
    this.getListData()
  },
  methods : {
    getListData() {
      this.axios.get('/static/data.json').then((res) => {
        this.List = res.data.List
        this.Slides = res.data.slides
      })
    },

    changeSlide(current) {
      console.log('当前轮播图序号为:' + current)
    },
    clickHandler(item, index) {
      console.log(item, index)
    }
  },
}
</script>

<style lang="stylus">
@import "assets/stylus/base"
</style>



